import styled from "styled-components";

import { colors, fontFamily, fontSizes } from "../styleguide";

export interface StyledTypographyProps {
	// these are transient props so that they are applied as css and not as attributes on the element
	$fontSize?: string;
	$fontWeight?: number | string;
	$fontFamily?: string;
	color?: string;
	lineHeight?: string;
	align?: string;
	showEllipsis?: boolean;
	/** margin shorthand notation
	 * @example margin: 25px 50px 75px 100px;
	 * @link https://www.w3schools.com/css/css_margin.asp
	 */
	m?: string
	mt?: string;
	mb?: string;
	ml?: string;
	mr?: string;
}

export const Text = styled.div<StyledTypographyProps>`
	font-family: ${({ $fontFamily = fontFamily.larsseit }) => $fontFamily};
	color: ${({ color = colors.black }) => color};
	font-size: ${({ $fontSize = fontSizes.s }) => $fontSize};

	line-height: ${({ lineHeight }) => lineHeight};
	text-transform: none;
	font-weight: ${({ $fontWeight }) => $fontWeight};
	text-align: ${({ align }) => align};
	margin: ${({ m, mb, mt, mr, ml }) => {
		if (!m && !mt && !mb && !mr && !ml) {
			return "0px";
		}

		if (m) {
			return m;
		}

		return `${mt || "0px"} ${mr || "0px"} ${mb || "0px"} ${ml || "0px"}`;
	}};

	${({ showEllipsis }) => showEllipsis && ({
		whiteSpace: "nowrap",
		overflow: "hidden",
		textOverflow: "ellipsis",
	})}
`;
